import axios from 'axios';
import QS from 'qs';
import router from '../router/index.js'
import {Message} from 'element-ui'
import ERROR from '../common/errorCode.js'
import $ from 'jquery'

//
axios.defaults.timeout = 20000;
//post (post)
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
//URL
//axios.defaults.baseURL = "/";

//
axios.interceptors.response.use(function (response) {
    let path = router.currentRoute.path;
    if(response.status == 200){
        let res = response.data;
        if(res.code == ERROR.UNAUTHORIZED){//
            if(!path.endsWith("/login")){//
                //, /admin/login, /store/login /delivery/login
                let arr = ['admin','store','delivery'];
                let root = path.split("/").filter(v=>v).shift();
                //console.log(root);
                if(arr.indexOf(root)<0){
                    //console.log($(window));
                    $(window).trigger("show-login");
                }else{
                    router.push({
                        path: '/'+[root,'login'].filter(v=>v).join("/"),
                        query: {
                            from: path
                        }
                    })
                }
            }
        }else if(res.code == ERROR.PERMISSION_DENIED){//
            Message({
                message: '',
                type: 'warning'
            })
        }
    }
    return response;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

// get
const get = (url, params,config={}) => {
	return new Promise((resolve, reject) => {
		axios.get(url, {
            params: params,
            ...config
		}).then(res => {
			//,
			resolve(res.data);
		}).catch(err => {
			reject(err.data);
		})
	});
}

// post
const post = (url, params,config={}) => {
    if(typeof params !== 'string') params = QS.stringify(params, { arrayFormat: 'repeat' });
	return new Promise((resolve, reject) => {
		axios.post(url, params,config).then(res => {
			//,
			resolve(res.data);
		}).catch(err => {
			reject(err.data);
		})
	});
}
export {
    axios
}
export default {
	get,
    post
}
