              </el-tab-pane>
              <!-- <el-tab-pane label="" name="menuPreview"></el-tab-pane> -->
              <el-tab-pane label="" name="searchResult">
                <i class="el-icon-refresh refresh-table" @click="getSearchInfo(true)"></i>
                <div class="table-div jquery-scrollbar-element scrollbar-macosx">
                  <div class="inline-block">
                    <el-table :data="searchInfo.tableData" style="width: 100%" border @expand-change="updateScrollbar">
                      <el-table-column type="expand">
                        <template slot-scope="props">
                          <el-form label-position="left" inline class="demo-table-expand">
                            <el-form-item
                              v-for="(v,k) in tableInfo.stringMap"
                              :key="v.id"
                              :label="tableInfo.stringMap[k]"
                            >
                              <span>{{ props.row[k] }}</span>
                            </el-form-item>
                            <el-form-item label="">
                              <el-avatar shape="square" :size="100" fit="cover" :src="props.row['logo']"></el-avatar>
                            </el-form-item>
                          </el-form>
                        </template>
                      </el-table-column>
                      <el-table-column label=" ID" prop="id"></el-table-column>
                      <el-table-column label="" prop="storeName"></el-table-column>
                      <el-table-column label="" prop="ownerName"></el-table-column>
                      <el-table-column label="" prop="phone"></el-table-column>
                      <el-table-column label="" prop="createTime"></el-table-column>
                      <el-table-column label="" prop="updateTime"></el-table-column>
                      <el-table-column label="" width="300">
                        <template slot-scope="scope">
                          <el-button
                            type="warning"
                            icon="el-icon-setting"
                            title=""
                            @click="chroot(scope.row.id)"
                            size="small"
                          ></el-button>
                          <el-button
                            type="primary"
                            icon="el-icon-edit"
                            title=""
                            @click="showEditStore(scope.$index)"
                            size="small"
                          ></el-button>
                          <el-button
                            type="danger"
                            icon="el-icon-delete"
                            title=""
                            @click="deleteStore(scope.$index)"
                            size="small"
                          ></el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </div>
                <div class="pager-div">
                  <el-pagination
                    @size-change="changeSearchPageSize"
                    @current-change="changeSearchPage"
                    :current-page="searchInfo.page_num"
                    :page-sizes="[6, 8, 10, 15, 20, 30]"
                    :page-size="searchInfo.page_size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="searchInfo.total_num"
                    :pager-count="5"
                    size="small"
                  ></el-pagination>
                </div>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <el-dialog title="" :visible.sync="dialog.addStoreVisble" :modal-append-to-body="false">
          <el-form :model="dialog.newStore" :rules="dialog.rules" ref="addStoreForm">
            <el-form-item label="" :label-width="'120px'">
              <el-select v-model="dialog.newStore.ownerId" filterable placeholder="">
                <el-option
                  v-for="item in userList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="storeName">
              <el-input v-model="dialog.newStore.storeName" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="address">
              <el-input v-model="dialog.newStore.address" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="phone">
              <el-input v-model="dialog.newStore.phone" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="description">
              <el-input v-model="dialog.newStore.description" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="minCost">
              <el-input v-model="dialog.newStore.minCost" placeholder=">=0" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="postFee">
              <el-input v-model="dialog.newStore.postFee" placeholder=">=0" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'">
              <el-upload class="upload-image" :on-success="uploadIconSuccess" :action="uploadUrl" :limit="1"
              name="file" list-type="picture-card" :before-upload="beforeIconUpload" 
              :on-exceed="uploadExcced">
                <i class="el-icon-plus"></i>
              </el-upload>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog.addStoreVisble = false"> </el-button>
            <el-button type="primary" @click="addStore()"> </el-button>
          </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialog.editStoreVisible" :modal-append-to-body="false">
          <el-form :model="dialog.editStore" :rules="dialog.rules" ref="editStoreForm">
            <el-form-item label="" :label-width="'120px'">
              <el-select v-model="dialog.editStore.ownerId" filterable placeholder="">
                <el-option
                  v-for="item in userList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="storeName">
              <el-input v-model="dialog.editStore.storeName" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="address">
              <el-input v-model="dialog.editStore.address" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="phone">
              <el-input v-model="dialog.editStore.phone" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="description">
              <el-input v-model="dialog.editStore.description" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="minCost">
              <el-input v-model="dialog.editStore.minCost" placeholder=">=0" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'" prop="postFee">
              <el-input v-model="dialog.editStore.postFee" placeholder=">=0" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="'120px'">
              <el-upload class="upload-image"  :on-success="editIconSuccess" :action="uploadUrl" :limit="1"
          name="file" list-type="picture-card" :before-upload="beforeIconUpload" :on-exceed="uploadExcced"
          :file-list="dialog.editStore.fileList">
                <i class="el-icon-plus"></i>
              </el-upload>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog.editStoreVisible = false"> </el-button>
            <el-button type="primary" @click="editStore()"> </el-button>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../common/http.js";
export default {
  data() {
    let notNull = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    let isNumber = (rule, value, callback) => {
      if (!(value+'')||value<0) {
        callback(new Error("0"));
      } else {
        callback();
      }
    }
    let data = {
      host: window.location.protocol +"//"+window.location.host,
      uploadUrl: '/store/uploadIcon',
      userList: [],
      dialog: {
        addStoreVisble: false,
        editStoreVisible: false,
        newStore: {
          ownerId: "",
          storeName: "",
          address: "",
          phone: "",
          description: "",
          minCost: "",
          postFee: "",
          logo: ""
        },
        editStore: {
          ownerId: "",
          storeName: "",
          address: "",
          phone: "",
          description: "",
          minCost: "",
          postFee: "",
          logo: ""
        },
        rules: {
          ownerName: [
            {
              validator: notNull,
              trigger: "change"
            }
          ],
          storeName: [
            {
              validator: notNull,
              trigger: "blur"
            }
          ],
          address: [
            {
              validator: notNull,
              trigger: "blur"
            }
          ],
          phone: [
            {
              validator: notNull,
              trigger: "blur"
            }
          ],
          description: [
            {
              validator: notNull,
              trigger: "blur"
            }
          ],
          minCost: [
            {
              validator: isNumber,
              trigger: "blur"
            }
          ],
          postFee: [
            {
              validator: isNumber,
              trigger: "blur"
            }
          ],
        }
      },
      activeTab: "menuList",
      searchInfo: {
        keyword: "",
        page_num: 1,
        page_size: 8,
        total_num: 1,
        tableData: []
      },
      keyword: "",
      tableInfo: {
        page_num: 1,
        page_size: 8,
        total_num: 1,
        stringMap: {
          id: "ID",
          ownerId: "ID",
          storeName: "",
          phone: "",
          address: "",
          description: "",
          minCost: "",
          postFee: "",
          //productIcon: "",
          updateTime: "",
          createTime: ""
        },
        tableData: [
          /*{sellerId ...}*/
        ]
      }
    };
    return data;
  },
  mounted() {
    this.$(".jquery-scrollbar-element").scrollbar();
    this.getTableInfo();
    this.getUserList();
  },
  methods: {

    updateScrollbar(row,rows){
      this.$nextTick(()=>{
        if(rows.length==0||row===true){
          this.$('.table-div').updateScrollbars(true)
        }
      });
    },
    chroot(id){
      axios.get("/admin/store/chroot",{id}).then(res=>{
        if(res&&res.code==1){
          this.$router.push('/store/menu');
        }
      })
    },
    getUserList(){
      axios.get('/admin/user/findAll').then(res=>{
        if(res&&res.code==1){
          if(res.data instanceof Array){
            res.data.forEach(v=>{
              let name= v.nickname || v.username;
              v.label = `ID:${v.id} :${name} :${v.phone}`;
              v.value = v.id;
            })
            this.userList = res.data;
          }
        }
      })
    },
    uploadExcced(){
      this.$message({message:'',type: 'warning'});
    },
    beforeIconUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }
      return isLt2M;
    },
    uploadIconSuccess(res){
      //this.newProduct.imgUrl = 
      if(res&&res.code==1){
        this.dialog.newStore.logo = res.data;
      }
    },
    editIconSuccess(res){
      if(res&&res.code==1){
        this.dialog.editStore.logo = res.data;
      }
    },
    changeSearchPage(page_num) {
      this.searchInfo.page_num = page_num;
      this.getSearchInfo(true);
    },
    changeSearchPageSize(page_size) {
      this.searchInfo.page_size = page_size;
      this.getSearchInfo(true);
    },
    changePage(page_num) {
      this.tableInfo.page_num = page_num;
      this.getTableInfo();
    },
    changePageSize(page_size) {
      this.tableInfo.page_size = page_size;
      this.getTableInfo();
    },
    updateInfo() {
      //console.log(this.activeTab);
      if (this.activeTab == "menuList") {
        this.getTableInfo();
      } else {
        this.getSearchInfo(true);
      }
    },
    getTableInfo(isSearch = false) {
      let tb = !isSearch ? this.tableInfo : this.searchInfo,
        param = {};
      if (isSearch) {
        param = {
          keyword: this.searchInfo.keyword
        };
      }
      //
      axios
        .get("/admin/store/list", {
          page_num: tb.page_num,
          page_size: tb.page_size,
          ...param
        })
        .then(res => {
          if (res && res.code == 1 && res.data) {
            if(res.data&&res.data.tableData instanceof Array){
              res.data.tableData.forEach( v=>{
                let user = res.data.userMap[v.ownerId];
                v.ownerName = user.nickname || user.username;
              })
            }
            if (isSearch) {
              this.searchInfo = Object.assign(this.searchInfo, res.data);
            } else {
              this.tableInfo = Object.assign(this.tableInfo, res.data);
            }
          }
        });
    },
    getSearchInfo(cacheKeyword = false) {
      if (!cacheKeyword) {
        this.searchInfo.keyword = this.keyword;
      }
      this.activeTab = "searchResult";
      this.getTableInfo(true);
    },
    //
    showaddStore(){
      this.dialog.addStoreVisble = true;
    },
    showEditStore(index) {
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let data = table.tableData[index];
      this.dialog.editStore = {...data,fileList:[{name: data.logo.split('/').pop(),url: data.logo}]};
      this.dialog.editStoreVisible = true;
      console.log(this.dialog.editStore)
    },
    addStore(){
      this.dialog.addStoreVisble = false;
      this.editStore(true);
    },
    editStore(isAdd=false){
      if(!isAdd) this.dialog.editStoreVisible = false;
      let act = isAdd ? this.dialog.newStore : this.dialog.editStore;
      let action = isAdd ? '' : '';
      let ref = isAdd ? this.$refs["addStoreForm"] : this.$refs["editStoreForm"];
      let store = {...act};
      delete store.updateTime;
      delete store.createTime;
      ref.validate(valid => {
        if(valid){
          axios.post('/admin/store/save',store).then(res=>{
            if (res && res.code == 1) {
              this.$message({ message: action+"", type: "success" });
              if(isAdd){
                ref.resetFields();
              }
              this.updateInfo();
            } else {
              this.$alert(res.tips, action+"", {
                confirmButtonText: ""
              });
            }
          });
        } else {
          this.$alert("", "", {
            confirmButtonText: ""
          });
          return false;
        }
      });
    },
    deleteStore(index) {
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let id = table.tableData[index].id;
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios.post('/admin/store/delete',{id}).then((res)=>{
          if(res&&res.code==1){
            this.$message({message: '',type: 'success'});
            this.updateInfo();
          }else{
            this.$alert(res.tips,"",{confirmButtonText: ''})
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    }
  }
};
</script>

