<template>
  <el-drawer
    title=""
    :append-to-body="true"
    :visible.sync="isVisible"
    size="75%"
  >
    <div class="out-box">
      <div class="mb10">
        <el-button type="primary" size="small" @click="showAddAddress"
          ></el-button
        >
      </div>
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column label=" ID" prop="id"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="address"></el-table-column>
        <el-table-column label="" prop="phone"></el-table-column>
        <el-table-column label="" prop="createTime"></el-table-column>
        <el-table-column label="" prop="updateTime"></el-table-column>
        <el-table-column label="" width="220">
          <template slot-scope="scope">
            <el-button
              type="success"
              @click="selectAddress(scope.$index)"
              size="small"
              ></el-button
            >
            <el-button
              type="primary"
              @click="showEditAddress(scope.$index)"
              size="small"
              ></el-button
            >
            <el-button
              type="danger"
              @click="deleteAddress(scope.$index)"
              size="small"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      title=""
      :visible.sync="dialog.addAddressVisible"
      :append-to-body="true"
      :modal-append-to-body="false"
    >
      <el-form :model="dialog.newAddress" ref="addAddressForm"  :rules="rules">
        <el-form-item label="" :label-width="'120px'" prop="name">
          <el-input
            v-model="dialog.newAddress.name"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="'120px'" prop="address">
          <el-input
            v-model="dialog.newAddress.address"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="'120px'" prop="phone">
          <el-input
            v-model="dialog.newAddress.phone"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog.addAddressVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="
            dialog.addAddressVisible = false;
            addAddress();
          "
          > </el-button
        >
      </div>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialog.editAddressVisible"
      :append-to-body="true"
      :modal-append-to-body="false"
    >
      <el-form :model="dialog.editAddress" ref="editAddressForm" :rules="rules">
        <el-form-item label="" :label-width="'120px'" prop="name">
          <el-input
            v-model="dialog.editAddress.name"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="'120px'" prop="address">
          <el-input
            v-model="dialog.editAddress.address"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="'120px'" prop="phone">
          <el-input
            v-model="dialog.editAddress.phone"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog.editAddressVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="
            dialog.editAddressVisible = false;
            editAddress();
          "
          > </el-button
        >
      </div>
    </el-dialog>
  </el-drawer>
</template>
<style lang="scss" scoped>
.out-box {
  padding: 10px 24px;
}
.mb10 {
  margin-bottom: 10px;
}
</style>
<script>
import axios from '../common/http.js'
export default {
  props: ["visible"],
  watch: {
    visible(value) {
      this.isVisible = value;
      if(value) this.getTableData();
    },
    isVisible(value) {
      this.$emit("change", value);
    },
  },
  data() {
    let map = {
      name: "",
      address: "",
      phone: "",
    };
    let rules = {};
    for (let k in map) {
      rules[k] = [
        {
          validator: (rule, value, callback) => {
            if (!value) {
              callback(new Error(map[k]));
            } else {
              callback();
            }
          },
          trigger: "blur",
        },
      ];
    }
    return {
      isVisible: this.visible,
      rules,
      dialog: {
        addAddressVisible: false,
        editAddressVisible: false,
        newAddress: {
          name: "",
          address: "",
          phone: "",
        },
        editAddress: {
          name: "",
          address: "",
          phone: "",
        },
      },
      tableData: [],
    };
  },
  mounted(){
    this.getTableData();
  },
  methods: {
    getTableData(){
      return axios.get('/user/address/list').then(res => {
        if(res&&res.code==1){
          this.tableData = res.data;
        }
      })
    },
    showAddAddress() {
      this.dialog.addAddressVisible = true;
    },
    showEditAddress(index) {
      this.dialog.editAddress = this.tableData[index];
      this.dialog.editAddressVisible = true;
    },
    selectAddress(index) {
      this.$emit("select",this.tableData[index]);
    },
    deleteAddress(index) {
      let id = this.tableData[index].id;
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios.post('/user/address/delete',{id}).then((res)=>{
          if(res&&res.code==1){
            this.$message({message: '',type: 'success'});
            this.getTableData();
          }else{
            this.$alert(res.tips,"",{confirmButtonText: ''})
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    },
    addAddress(edit = false) {
      let addr = edit ? this.dialog.editAddress : this.dialog.newAddress;
      let form = edit ? this.$refs['editAddressForm'] : this.$refs['addAddressForm'];
      let action = edit ? '' : '';
      form.validate(valid=>{
        if(valid){
          axios.post('/user/address/save',addr).then(res => {
            if(res&&res.code==1){
              this.getTableData();
              this.addAddressVisible = false;
              this.editAddressVisible = false;
              this.$message({type:'success',message:action+''});
            }else{
              this.$alert(res.tips,action+"",{confirmButtonText: ''})
            }
          })
        }else{
          this.$alert('',"",{confirmButtonText: ''})
        }
      })
    },
    editAddress() {
      this.addAddress(true);
    },
  },
};
</script>
