<template>
  <div class="fit-parent">
    <div class="account-manage-div jquery-scrollbar-element scrollbar-macosx">
      <div class="padding24">
        <div class="title-div">
          <h2 class="title-text">
            <span></span>
          </h2>
        </div>
        <div class="accounts-div">
          <div class="tools-div">
            <el-form :inline="true" :model="searchInfo" class="demo-form-inline">
              <el-form-item label=":">
                <el-input
                  size="small"
                  class="search-input"
                  placeholder=""
                  prefix-icon="el-icon-search"
                  @keydown.enter.native="getSearchInfo()"
                  v-model="keyword"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button
                  size="small"
                  plain
                  type="primary"
                  @click="getSearchInfo()"
                  icon="el-icon-search"
                ></el-button>
              </el-form-item>
              <el-form-item>
                <el-button
                  size="small"
                  plain
                  @click="dialog.addAccountVisible=true"
                  icon="el-icon-plus"
                ></el-button>
              </el-form-item>
            </el-form>
          </div>
          <div class="account-table">
            <el-tabs type="border-card" v-model="activeTab" @tab-click="updateInfo">
              <el-tab-pane label="" name="menuList">
                <i class="el-icon-refresh refresh-table" @click="getTableInfo()"></i>
                <div class="table-div jquery-scrollbar-element scrollbar-macosx">
                  <div class="inline-block">
                    <el-table :data="tableInfo.tableData" border style="width: 100%">
                      <el-table-column label=" ID" prop="id"></el-table-column>
                      <el-table-column label="" prop="username"></el-table-column>
                      <el-table-column label="" prop="nickname"></el-table-column>
                      <el-table-column label="" prop="phone"></el-table-column>
                      <el-table-column label="" prop="createTime"></el-table-column>
                      <el-table-column label="" width="100">
                        <template slot-scope="scope">
                          <el-button
                            type="primary"
                            icon="el-icon-edit"
                            circle
                            title=""
                            @click="showEditAccount(scope.$index)"
                            size="small"
                          ></el-button>
                          <el-button
                            type="danger"
                            icon="el-icon-delete"
                            circle
                            title=""
                            @click="deleteAccount(scope.$index)"
                            size="small"
                          ></el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </div>
                <div class="pager-div">
                  <el-pagination
                    @size-change="changePageSize"
                    @current-change="changePage"
                    :current-page="tableInfo.page_num"
                    :page-sizes="[6, 8, 10, 15, 20, 30]"
                    :page-size="tableInfo.page_size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="tableInfo.total_num"
                    :pager-count="5"
                    size="small"
                  ></el-pagination>
                </div>
              </el-tab-pane>
              <!-- <el-tab-pane label="" name="menuPreview"></el-tab-pane> -->
              <el-tab-pane label="" name="searchResult">
                <i class="el-icon-refresh refresh-table" @click="getSearchInfo(true)"></i>
                <div class="table-div jquery-scrollbar-element scrollbar-macosx">
                  <div class="inline-block">
                    <el-table :data="searchInfo.tableData" style="width: 100%" border>
                      <el-table-column label=" ID" prop="id"></el-table-column>
                      <el-table-column label="" prop="username"></el-table-column>
                      <el-table-column label="" prop="nickname"></el-table-column>
                      <el-table-column label="" prop="phone"></el-table-column>
                      <el-table-column label="" prop="createTime"></el-table-column>
                      <el-table-column label="" width="100">
                        <template slot-scope="scope">
                          <el-button
                            type="primary"
                            icon="el-icon-edit"
                            circle
                            title=""
                            @click="showEditAccount(scope.$index)"
                            size="small"
                          ></el-button>
                          <el-button
                            type="danger"
                            icon="el-icon-delete"
                            circle
                            title=""
                            @click="deleteAccount(scope.$index)"
                            size="small"
                          ></el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </div>
                <div class="pager-div">
                  <el-pagination
                    @size-change="changeSearchPageSize"
                    @current-change="changeSearchPage"
                    :current-page="searchInfo.page_num"
                    :page-sizes="[6, 8, 10, 15, 20, 30]"
                    :page-size="searchInfo.page_size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="searchInfo.total_num"
                    :pager-count="5"
                    size="small"
                  ></el-pagination>
                </div>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <el-dialog title="" :visible.sync="dialog.addAccountVisible" :modal-append-to-body="false">
          <el-form :model="dialog.newAccount" :rules="dialog.rules" ref="addAccountForm">
            <el-form-item label="" :label-width="'140px'">
              <el-select v-model="dialog.newAccount.userId" filterable placeholder="">
                <el-option
                  v-for="item in userList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="'140px'">
              <el-select v-model="dialog.newAccount.storeIds" style="width:300px;" multiple filterable placeholder="">
                <el-option
                  v-for="item in storeList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog.addAccountVisible = false"> </el-button>
            <el-button type="primary" @click="addAccount()"> </el-button>
          </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialog.editAccountVisible" :modal-append-to-body="false">
          <el-form :model="dialog.account" :rules="dialog.rules" ref="editAccountForm">
            <el-form-item label="" :label-width="'140px'">
              <el-select v-model="dialog.account.userId" disabled placeholder="">
                <el-option
                  v-for="item in userList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="'140px'">
              <el-select v-model="dialog.account.storeIds" style="width:300px;" multiple filterable placeholder="">
                <el-option
                  v-for="item in storeList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog.editAccountVisible = false"> </el-button>
            <el-button type="primary" @click="editAccount()"> </el-button>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../common/http.js";
export default {
  data() {
    let data = {
      userList: [],
      storeList: [],
      dialog: {
        addAccountVisible: false,
        editAccountVisible: false,
        newAccount: {
          userId: "",
          storeIds: []
        },
        account: {
          userId: "",
          storeIds: []
        },
        rules: {
          
        }
      },
      activeTab: "menuList",
      searchInfo: {
        keyword: "",
        page_num: 1,
        page_size: 8,
        total_num: 1,
        tableData: []
      },
      keyword: "",
      tableInfo: {
        page_num: 1,
        page_size: 8,
        total_num: 1,
        tableData: [
          /*{sellerId ...}*/
        ]
      }
    };
    return data;
  },
  mounted() {
    this.$(".jquery-scrollbar-element").scrollbar();
    this.getTableInfo();
    this.getUserList();
    this.getStoreList();
  },
  methods: {
    getStoreList(){
      axios.get('/admin/store/findAll').then(res=>{
        if(res&&res.code==1){
          if(res.data instanceof Array){
            res.data.forEach(v=>{
              v.label = `ID:${v.id} :${v.storeName} :${v.phone}`;
              v.value = v.id;
            })
            this.storeList = res.data;
          }
        }
      })
    },
    getUserList(){
      axios.get('/admin/user/findAll').then(res=>{
        if(res&&res.code==1){
          if(res.data instanceof Array){
            res.data.forEach(v=>{
              let name= v.nickname || v.username;
              v.label = `ID:${v.id} :${name} :${v.phone}`;
              v.value = v.id;
            })
            this.userList = res.data;
          }
        }
      })
    },
    changeSearchPage(page_num) {
      this.searchInfo.page_num = page_num;
      this.getSearchInfo(true);
    },
    changeSearchPageSize(page_size) {
      this.searchInfo.page_size = page_size;
      this.getSearchInfo(true);
    },
    changePage(page_num) {
      this.tableInfo.page_num = page_num;
      this.getTableInfo();
    },
    changePageSize(page_size) {
      this.tableInfo.page_size = page_size;
      this.getTableInfo();
    },
    updateInfo() {
      //console.log(this.activeTab);
      if (this.activeTab == "menuList") {
        this.getTableInfo();
      } else {
        this.getSearchInfo(true);
      }
    },
    getTableInfo(isSearch = false) {
      let tb = !isSearch ? this.tableInfo : this.searchInfo,
        param = {};
      if (isSearch) {
        param = {
          keyword: this.searchInfo.keyword
        };
      }
      //
      axios
        .get("/admin/delivery/list", {
          page_num: tb.page_num,
          page_size: tb.page_size,
          ...param
        })
        .then(res => {
          if (res && res.code == 1 && res.data) {
            if(res.data&&res.data.tableData instanceof Array){
              let map = {};
              res.data.tableData.forEach(v=>{
                if(map[v.user.id]){
                  map[v.user.id].storeIds.push(v.store.id);
                  let ct = map[v.user.id].ct;
                  if(v.createTime > ct) map[v.user.id].ct = v.createTime;
                }else{
                  map[v.user.id] = {...v.user,storeIds:[v.store.id],ct:v.createTime}
                }
              });
              console.log(map);
              let arr = [];
              for(let k in map) arr.push(map[k]);
              arr = arr.sort((a,b)=>{
                a.ct < b.ct;
              });
              res.data.tableData = arr;
            }
            if (isSearch) {
              this.searchInfo = Object.assign(this.searchInfo, res.data);
            } else {
              this.tableInfo = Object.assign(this.tableInfo, res.data);
            }
          }
        });
    },
    getSearchInfo(cacheKeyword = false) {
      if (!cacheKeyword) {
        this.searchInfo.keyword = this.keyword;
      }
      this.activeTab = "searchResult";
      this.getTableInfo(true);
    },
    //
    showAddAccount(){
      this.dialog.addAccountVisible = true;
    },
    showEditAccount(index) {
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let data = table.tableData[index];
      this.dialog.account = {...data,userId: data.id};
      this.dialog.editAccountVisible = true;

    },
    addAccount(){
      this.dialog.addAccountVisible = false;
      this.editAccount(true);
    },
    editAccount(isAdd=false){
      if(!isAdd) this.dialog.editAccountVisible = false;
      let act = isAdd ? this.dialog.newAccount : this.dialog.account;
      let action = isAdd ? '' : '';
      let ref = isAdd ? this.$refs["addAccountForm"] : this.$refs["editAccountForm"];
      let arr = act.storeIds || [];
      if(!arr||arr.length <=0){
        this.$alert("", "", {
          confirmButtonText: ""
        });
        return;
      }
      if(!act.userId){
        this.$alert("", "", {
          confirmButtonText: ""
        });
        return;
      }
      let param = JSON.stringify(arr.map(v=>({storeId:v,userId:act.userId})) );
      console.log(param,arr,act);
      ref.validate(valid => {
        if(valid){
          axios.post('/admin/delivery/save',param,
          {headers:{'Content-Type':'application/json;charset=utf-8'}}).then(res=>{
            if (res && res.code == 1) {
              this.$message({ message: action+"", type: "success" });
              if(isAdd){
                ref.resetFields();
              }
              this.updateInfo();
            } else {
              this.$alert(res.tips, action+"", {
                confirmButtonText: ""
              });
            }
          });
        } else {
          this.$alert("", "", {
            confirmButtonText: ""
          });
          return false;
        }
      });
    },
    deleteAccount(index) {
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let userId = table.tableData[index].id;
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        axios.post('/admin/delivery/delete',{userId}).then((res)=>{
          if(res&&res.code==1){
            this.$message({message: '',type: 'success'});
            this.updateInfo();
          }else{
            this.$alert(res.tips,"",{confirmButtonText: ''})
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    }
  }
};
</script>

